.borderedButton {
  padding: 8px 20px;
  font-size: 14px;
  border-radius: 8px;
  width: calc(80% - 48px);
  cursor: pointer;
  border: none;
  color: #fff;
  font-weight: 500;
  background-color: #3775bb;
  margin: var(--margin);
  padding: 12px 0px;
  transition: 0.2s;
}

.borderedButton:hover {
  background-color: rgb(0, 88, 151);
  margin: var(--margin);
}

.borderedButton:active {
  background-color: #004c82;
}

.smallButton {
  /* 글쓰기 버튼의 크기 더하기 패딩값 */
  bottom: -32px;
  width: 100%;
  height: 50px;
  font-size: 24px;
  color: #ffffff;
  cursor: pointer;
  border-radius: 4px;
  border: none;
  padding: 0;
  background-color: #3775bb;
}

.smallButton:hover {
  transition: 0.2s;
  color: #ffffff;
  cursor: pointer;
  background-color: #185aa5;
  padding: 0;
}

.smallButton:active {
  transition: 0.2s;
  color: #ffffff;
  cursor: pointer;
  background-color: #005ac0;
}
